import React, {useState} from 'react'
import Affairs from './affairs/Affairs'
import s2 from '../../s1-main/App.module.css'

/*
* 1 -   AffairPriorityType, AffairType
* 2 -     defaultAffairs
* 3 -      filterAffairs    
* 4 -   3   deleteAffair
* 5 -     useState  affairs
* 6 -      deleteAffairCallback
* 7 -   Affairs.tsx   
* 8 -   Affairs.tsx    setAll, setHigh, setMiddle, setLow
* 9 -   Affair.tsx   
* 10 -   Affair.tsx   deleteCallback  
* 11 -   Affair.tsx   
* */

// types
export type AffairPriorityType = 'high' | 'low' | 'middle'
export type AffairType = {
  _id: number;
  name: string;
  priority: AffairPriorityType
}
export type FilterType = 'all' | AffairPriorityType

// constants
const defaultAffairs: AffairType[] = [
  {_id: 1, name: 'React', priority: 'high'}, //     name     , ...priority  !
  {_id: 2, name: 'anime', priority: 'low'},
  {_id: 3, name: 'games', priority: 'low'},
  {_id: 4, name: 'work', priority: 'high'},
  {_id: 5, name: 'html & css', priority: 'middle'},
]

// pure helper functions
export const filterAffairs = (affairs: AffairType[], filter: FilterType): AffairType[] | undefined => {
  if (filter === 'all') return affairs
  else if (filter === 'high') {
    return affairs.filter(a => a.priority === 'high')
  } else if (filter === 'middle') {
    return affairs.filter(a => a.priority === 'middle')
  } else if (filter === 'low') {
    return affairs.filter(a => a.priority === 'low')
  }
}
export const deleteAffair = (affairs: AffairType[], _id: number): AffairType[] => {
  return affairs.filter(a => a._id !== _id);
}

function HW2() {
  const [affairs, setAffairs] = useState<Array<AffairType>>(defaultAffairs)
  const [filter, setFilter] = useState<FilterType>('all')

  const filteredAffairs = filterAffairs(affairs, filter)
  const deleteAffairCallback = (_id: number) => setAffairs(deleteAffair(affairs, _id))

  return (
    <div id={'hw2'}>
      <div className={s2.hwTitle}>Homework #2</div>
      <div className={s2.hw}>
        <Affairs
          data={filteredAffairs}
          setFilter={setFilter}
          deleteAffairCallback={deleteAffairCallback}
          filter={filter}
        />
      </div>
    </div>
  )
}

export default HW2
